import React, {useState} from 'react'
import Carousel from "react-bootstrap/Carousel";
import fImg from '../assets/1.jpg'
import sImg from '../assets/2.jpg'
import tImg from '../assets/3.jpg'
import foImg from '../assets/4.jpg'
import fiImg from '../assets/5.jpg'
// import 'bootstrap/dist/css/bootstrap.css';
import './Swipe.scss'
import {Animated} from "react-animated-css";

function Swipe() {
    const [index, setIndex] = useState(0);
    const handleSelect = (selectedIndex, e) => {
        setIndex(selectedIndex);
    };
    const [nextIcon,setNextIcon] = useState(
        <div className='rightArrow'>
            {/*<Animated animationIn="fadeInDown" animationOut="fadeOut" initialVisible={false} animatePreScroll={false} animationInDuration={400} animationOutDuration={4000}>*/}
            {/*<svg className='swipeArrow' width="24" height="19" viewBox="0 0 24 19" fill="none" xmlns="http://www.w3.org/2000/svg">*/}
            {/*    <path d="M0.391113 10.9531L21.3911 10.9531L21.3911 7.95312L0.391113 7.95312L0.391113 10.9531Z" fill="white"/>*/}
            {/*    <path d="M12.362 2.73011L20.27 10.6382L22.3913 8.51686L14.4833 0.608793L12.362 2.73011Z" fill="white"/>*/}
            {/*    <path d="M22.3911 10.4534L14.4863 18.3582L12.365 16.2369L20.2698 8.33203L22.3911 10.4534Z" fill="white"/>*/}
            {/*    <path d="M23.3579 9.48655L22.3106 10.5339L20.1893 8.41249L21.2366 7.36523L23.3579 9.48655Z" fill="white"/>*/}
            {/*</svg>*/}
            <svg className='swipeArrow' width="22" height="17" viewBox="0 0 22 17" fill="none" xmlns="http://www.w3.org/2000/svg">
                <path d="M19.0264 7.39453L19.0259 9.60506L0.236892 9.60506L0.236892 7.39453L19.0264 7.39453Z" fill="white"/>
                <path d="M21.2373 8.55889L19.0293 9.6048L11.2899 2.24183L12.8512 0.762695L21.2373 8.55889Z" fill="white"/>
                <path d="M21.2373 8.44044L19.0293 7.39453L11.2899 14.7575L12.8512 16.2366L21.2373 8.44044Z" fill="white"/>
            </svg>


            <div className="goTopBg"/>
            {/*</Animated>*/}
        </div>
    )
    const [prevIcon,setPrevIcon] = useState(
        <div>
            {/*<Animated animationIn="fadeInDown" animationOut="fadeOut" initialVisible={false} animatePreScroll={false} animationInDuration={400} animationOutDuration={4000}>*/}
            {/*<svg className='swipeArrow' width="24" height="19" viewBox="0 0 24 19" fill="none" xmlns="http://www.w3.org/2000/svg">*/}
            {/*    <path d="M23.3579 8.01392L2.35791 8.01392L2.35791 11.0139L23.3579 11.0139L23.3579 8.01392Z" fill="white"/>*/}
            {/*    <path d="M11.3871 16.2369L3.479 8.32886L1.35768 10.4502L9.26575 18.3582L11.3871 16.2369Z" fill="white"/>*/}
            {/*    <path d="M1.35791 8.51369L9.2627 0.608889L11.384 2.73019L3.47923 10.635L1.35791 8.51369Z" fill="white"/>*/}
            {/*    <path d="M0.391113 9.48049L1.43837 8.43319L3.55969 10.5545L2.51243 11.6018L0.391113 9.48049Z" fill="white"/>*/}
            {/*</svg>*/}
            <svg className='swipeArrow' width="22" height="17" viewBox="0 0 22 17" fill="none" xmlns="http://www.w3.org/2000/svg">
                <path d="M2.44824 9.60449L2.44875 7.39397L21.2377 7.39397L21.2377 9.60449L2.44824 9.60449Z" fill="white"/>
                <path d="M0.237304 8.44013L2.44529 7.39422L10.1847 14.7572L8.62338 16.2363L0.237304 8.44013Z" fill="white"/>
                <path d="M0.237305 8.55858L2.44529 9.60449L10.1847 2.24151L8.62338 0.762387L0.237305 8.55858Z" fill="white"/>
            </svg>

            <div className="goTopBg"/>
            {/*</Animated>*/}
        </div>
    )


    return (
            <Carousel touch={true} nextIcon ={nextIcon} prevIcon={prevIcon} className='swipe' activeIndex={index} onSelect={handleSelect} style={{height:'600px'}}>
                <Carousel.Item style={{height:'600px', width:'1200px'}}>
                    <img
                        className="d-block w-100"
                        src={fImg}
                        alt="First slide"

                    />
                </Carousel.Item>
                <Carousel.Item style={{height:'600px', width:'1200px'}}>
                    <img
                        className="d-block w-100"
                        src={sImg}
                        alt="Second slide"

                    />
                </Carousel.Item>
                <Carousel.Item style={{height:'600px', width:'1200px'}}>
                    <img
                        className="d-block w-100"
                        src={tImg}
                        alt="Third slide"
                    />
                </Carousel.Item>
                <Carousel.Item style={{height:'600px', width:'1200px'}}>
                    <img
                        className="d-block w-100"
                        src={foImg}
                        alt="Third slide"
                    />
                </Carousel.Item>
                <Carousel.Item style={{height:'600px', width:'1200px'}}>
                    <img
                        className="d-block w-100"
                        src={fiImg}
                        alt="Third slide"
                    />
                </Carousel.Item>
            </Carousel>
    );
}

export default Swipe