import React, {useState} from 'react';
import ReactDOM from 'react-dom';
import Modal from 'react-modal';
import './Menu.scss'
import clz from "../assets/close.svg"
import style from "radium/es/components/style";
import {Route,NavLink, useLocation} from 'react-router-dom';
import ScrollAnimation from 'react-animate-on-scroll';
import { CSSTransition } from 'react-transition-group';
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faHeart} from "@fortawesome/free-solid-svg-icons";
import {Animated} from "react-animated-css";

const Menu = props =>{
    const [modalIsOpen, setModalIsOpen] = useState(false)
    return(
        <div className="menu">
            {/* eslint-disable-next-line no-unused-expressions */}
            <div className="headerRightBar" onClick={()=>{setModalIsOpen(!modalIsOpen)}}>
                <div className='bubleMenu' onClick={props.colorOpen} >
                    <div className="menu">MENU</div>
                    <div className="menuBar">
                        <div className="iron" id="i1"></div>
                        <div className="iron" id="i2"></div>
                        <div className="iron" id="i3"></div>
                    </div>
                </div>
                <div className='bubleClose' onClick={props.colorClose} >
                    <div className="menu">CLOSE</div>
                    <div className="menuBar">
                        <svg className='clz' width="22" height="22" viewBox="0 0 22 22" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <path className='markX' d="M19.2331 3.94279e-06L0 19.2331L2.1213 21.3544L21.3544 2.1213L19.2331 3.94279e-06Z" />
                            <path className='markX' d="M2.1307 7.45058e-06L0.00939941 2.12131L19.2425 21.3544L21.3638 19.2331L2.1307 7.45058e-06Z"/>
                        </svg>
                    </div>
                </div>
            </div>
            <CSSTransition in={modalIsOpen} classNames="dialog">
                <Modal
                    className="mymodal"
                    overlayClassName="myoverlay"
                    contentLabel="modal"
                    closeTimeoutMS={1000}
                    isOpen={modalIsOpen}
                >
                    {/*<div to="/menu" className="headerRightBar" onClick={()=>setModalIsOpen(false)}>*/}
                    {/*    <div className="menu" id="main">CLOSE</div>*/}
                    {/*    <div className="menuBar">*/}
                    {/*        <div className="close">*/}
                    {/*            <img  className="clz" src={clz}/>*/}
                    {/*        </div>*/}
                    {/*    </div>*/}
                    {/*</div>*/}
                    <div className='menuList'>

                        <div className='menuContent'>
                                <div className="menuTag">
                                    <Animated animationIn="fadeIn" animationOut="fadeOut" isVisible={true} animationInDuration={800} animationOutDuration={100}>
                                    MENU
                                    </Animated>
                                </div>
                                <NavLink to="/" exact className="liMenu projects1" onClick={()=>setModalIsOpen(false)}>
                                    <Animated animationIn="fadeInLeft" animationOut="fadeOut" isVisible={true} animationInDuration={800} animationOutDuration={10} animationInDelay={100}>
                                        <div onClick={props.colorClose}>Projects</div>
                                    </Animated>
                                </NavLink>
                                <NavLink to="/about" className="liMenu about1" onClick={()=>setModalIsOpen(false)}>
                                    <Animated animationIn="fadeInLeft" animationOut="fadeOut" isVisible={true} animationInDuration={800} animationOutDuration={10} animationInDelay={170}>
                                        <div onClick={props.colorClose}>About me</div>
                                    </Animated>
                                </NavLink>
                                <NavLink to="/contact" className="liMenu contact1" onClick={()=>setModalIsOpen(false)}>
                                    <Animated animationIn="fadeInLeft" animationOut="fadeOut" isVisible={true} animationInDuration={800} animationOutDuration={10} animationInDelay={240}>
                                        <div onClick={props.colorClose}>Contact</div>
                                    </Animated>
                                </NavLink>
                        </div>

                    </div>

                    <div className='corp' id='corpMenu'>
                        <Animated animationIn="fadeInUp" animationOut="fadeOut" isVisible={true} animationInDuration={600} animationOutDuration={100}>
                        <div className='infoMenu'>© 2020 — 2021 Daniil Zaozersky.
                            Made with <FontAwesomeIcon icon={faHeart} id='loveMenu'/> in St.Petersburg, Russia.</div>
                        </Animated>
                    </div>

                    <div className="menuOverlay" onClick={()=>setModalIsOpen(false)}>
                        <div className='menuOverlayColorClose' onClick={props.colorClose}></div>
                    </div>
                    {/*<div className="menuOverlay" onClick={}/>*/}
                </Modal>
            </CSSTransition>
        </div>
    )
}



export default Menu