import React,{ useState, useEffect} from 'react';
import emailjs from 'emailjs-com';
import ReactDOM from 'react-dom'
import Dropdown from "./Dropdown";
import './Form.scss'
import TextareaAutosize from 'react-textarea-autosize';
import{ init } from 'emailjs-com';
init("user_T59oxn1yzMWDeQGFRlpQn");

const idArr = ['b1s','b2s','b3b']
export default function Form() {
    const [selectVision,setSelectVision] = useState(undefined)
    function sendEmail(e) {
        e.preventDefault();

        emailjs.sendForm('service_pqlpoe5', 'template_gtkwfvv', e.target, 'user_T59oxn1yzMWDeQGFRlpQn')
            .then((result) => {
                console.log(result.text);
                console.log('lalsl')
                document.querySelector('.messageProgress').style.display = 'none'
                document.querySelector('.messageEnd').style.display = 'inherit'
            }, (error) => {
                console.log(error.text);
            });
    }
    // function dropdown(){
    //     setSelectVision({display: 'inherit'})
    // }
    // function dropback(){
    //     setSelectVision({display: 'none'})
    // }
    function onB(id){
            if (id == 'b1s' ){
                document.querySelector(`#b1s`).style.width = '0'
            }
            if (id == 'b2s' ){
                document.querySelector(`#b2s`).style.width = '0'
            }
            if (id == 'b3b' ){
                document.querySelector(`#b3b`).style.width = '0'
            }

    }
    function  onF(id){
        if (id == 'b1s' ){
            document.querySelector(`#b1s`).style.width = '20.6%'
        }
        if (id == 'b2s' ){
            document.querySelector(`#b2s`).style.width = '20.6%'
        }
        if (id == 'b3b' ){
            document.querySelector(`#b3b`).style.width = '43%'
        }
    }
    function handler(){
        if (document.querySelector('#user_name').value !== '' &&
            document.querySelector('#user_email').value !== '' &&
            document.querySelector('#message').value !== ''
        )
        {
            document.querySelector('.sendButton').style.display = 'none'
            document.querySelector('.messageProgress').style.display = 'inherit'
        }

        // document.querySelector('.loadSVG').style.transform = 'rotate(180deg)'
    }
    return (
        <form className="contact-form" onSubmit={sendEmail}>
                <div className='lineForm'>
                    <div className='smallBlock' onBlur={onB.bind(this, 'b1s')} onFocus={onF.bind(this, 'b1s')}>
                        <label className='labelContact'>Your Name</label>
                        <input type="text" name="user_name" className='inputSmall' id='user_name' placeholder="What's is your name?" required/>
                        <div id='b1s' className='blackBorder'/>
                    </div>
                    <div className='smallBlock' onBlur={onB.bind(this, 'b2s')} onFocus={onF.bind(this, 'b2s')}>
                        <label className='labelContact'>Your Email</label>
                        <input type="email" name="user_email" className='inputSmall' id='user_email' placeholder="What's is your email address?" required/>
                        <div id='b2s' className='blackBorder'/>
                    </div>
                </div>
                {/*<div className='smallBlock'>*/}

                    {/*<div className='containerSelect' data-btn="outside">*/}
                    {/*    <label className='labelContact'>Service</label>*/}
                        {/*<Dropdown*/}
                        {/*placeholder='What are you interested in?'*/}
                        {/*value={selectVision}*/}
                        {/*onChange={v => setSelectVision(v)}*/}
                        {/*options={["Apple", "Banana", "Orange", "Mango"]}*/}
                        {/*/>*/}
                    {/*</div>*/}

                    {/*<select name='service'  className='selectSmall'>*/}
                    {/*    <option value="" className="selectPlaceholder" disabled selected>What are you interested in?</option>*/}
                    {/*    <option value="hui">Монтаж видео  300р/ч.</option>*/}
                    {/*    <option value="2">Съёмка видео  200р/ч</option>*/}
                    {/*    <option value="3">Аренда техники  500р/ч</option>*/}
                    {/*    <option value="4">Графика  550р/ч</option>*/}
                    {/*</select>*/}
                {/*</div>*/}

                <div onBlur={onB.bind(this, 'b3b')} onFocus={onF.bind(this, 'b3b')}>
                    <label className='labelContact'>Message</label>
                    <TextareaAutosize
                        className='inputSmall'
                        name="message" required
                        style={{resize:'none'}}
                        id='message'
                        placeholder="What’s your message?"
                    />
                    <div id='b3b' className='blackBorder'/>
                </div>


            <div className='lineForm'>
                <button className='sendButton'  type="submit" onClick={handler.bind(this)}>
                    <svg width="20" height="14" viewBox="0 0 20 14" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <rect y="14" width="14" height="2" transform="rotate(-90 0 14)" fill="black"/>
                        <rect x="18" y="14" width="14" height="2" transform="rotate(-90 18 14)" fill="black"/>
                        <rect x="10.0156" y="9.35962" width="10.4773" height="2" transform="rotate(-150 10.0156 9.35962)" fill="black"/>
                        <rect width="10.8995" height="2" transform="matrix(0.866025 -0.5 -0.5 -0.866025 10 9.35962)" fill="black"/>
                        <rect x="18" y="14" width="17" height="2" transform="rotate(180 18 14)" fill="black"/>
                        <rect x="19" y="2" width="18" height="2" transform="rotate(180 19 2)" fill="black"/>
                    </svg>
                    <div className='sendText'>Send message</div>
                </button>
                <div className='messageProgress'>
                    {/*<svg className='loadSVG' width="17" height="17" viewBox="0 0 17 17" fill="none" xmlns="http://www.w3.org/2000/svg">*/}
                    {/*    <path d="M17 8.5C17 13.1944 13.1944 17 8.5 17C3.80558 17 0 13.1944 0 8.5C0 3.80558 3.80558 0 8.5 0C13.1944 0 17 3.80558 17 8.5Z" fill="white"/>*/}
                    {/*    <path d="M8 1C12.4183 1 16 4.58172 16 9" stroke="black" stroke-width="2"/>*/}
                    {/*</svg>*/}
                    <svg className='loadSVG' width="17" height="17" viewBox="0 0 17 17" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path d="M17 8.5C17 13.1944 13.1944 17 8.5 17C3.80558 17 0 13.1944 0 8.5C0 3.80558 3.80558 0 8.5 0C13.1944 0 17 3.80558 17 8.5Z" fill="white"/>
                        <path d="M8.5 1C12.5 1 17.5 4.5 15.5 11" stroke="black" stroke-width="2"/>
                    </svg>

                    <div className='sendText'>Sending...</div>
                </div>
                <div className='messageEnd'>
                    <svg width="18" height="14" viewBox="0 0 18 14" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path d="M4.1879 11.9711L16.1251 -0.000167444L17.5413 1.41203L5.60413 13.3833L4.1879 11.9711Z" fill="black"/>
                        <path d="M1.41461 6.38847L6.9917 11.969L5.57705 13.3827L-3.2994e-05 7.80225L1.41461 6.38847Z" fill="black"/>
                    </svg>
                    <div className='sendText'>Message sent. I will get back to you soon.</div>
                </div>
            </div>

        </form>
    );
}