import React from 'react'
import './projectBlock.scss'
import ScrollAnimation from 'react-animate-on-scroll';
import {Route,NavLink, useLocation} from 'react-router-dom';
const blockHover = props=>{
    return(
                <NavLink to={props.href} className="projectBlock__textContent__hover">
                    <ScrollAnimation animateIn="fadeInUp"  animateOnce={true} offset={0} duration={0.7}>
                        <div className="projectBlock__year_hover">{props.year}</div>
                    </ScrollAnimation>
                    <ScrollAnimation animateIn="fadeInUp"  animateOnce={true} offset={0} duration={0.7}>
                        <div className="projectBlock__title_hover">{props.title}</div>
                    </ScrollAnimation>
                </NavLink>
    )
}

export default blockHover