import React from 'react'
import './projectBlock.scss'
import ScrollAnimation from 'react-animate-on-scroll';
import {Route,NavLink, useLocation} from 'react-router-dom';
const Block = props=>{

    return(
        <div className="projectBlock">
            <NavLink to={props.href}>
                <div className="pb1bg"
                     style={props.url}
                />
                <div className="pbbgDark"/>
                <div className="projectBlock__textContent">
                    <div className="projectBlock__year">{props.year}</div>
                    <div className="projectBlock__title" style={props.color}>{props.title}</div>
                </div>
            </NavLink>
        </div>
    )
}

export default Block