import { useEffect } from "react";
import { useLocation } from "react-router-dom";

export default function MomentScroll() {
    const { key } = useLocation();
    console.log(useLocation())

    useEffect(() => {
        window.scrollTo(0, 0);
        if(document.querySelector('.bg1')){
            document.querySelector('.bg1').style.opacity = '1'
        }
        if(document.querySelector('.goTooBlock')){
            document.querySelector('.goTooBlock').style.display='none'
        }
    }, [key]);

    return null;
}